html,body{
  height: 100%;
}

h1{
  text-align: center;
}

input {
  color: #555;
  font-size: 15px;
  margin: 20px;
  width: 200px;
  border-radius: 5px;
  border-style: solid gray;
  border-width: 1px;
}

select{
  color: #555;
  font-size: 15px;
  margin: 20px;
  width: 120px;
  background: -webkit-linear-gradient(#FAFAFA, #F4F4F4 40%, #E5E5E5);  
}

.App {
  text-align: center;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

.input-form{
  text-align: center;
  padding: 10px;
}

.toggleClassButton{
  margin-left: 310px;
  font-size: 15px;
  width: 200px;
  border-radius: 5px;
  border-style: solid black;
  border-width: 1px;
}

.toggleClassButton:hover{
  color: white;
  cursor: pointer;
}

.class-selection-grid{
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  width: 95%;
  margin: auto;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-align: center;
      align-items: center;
}

.class-button{
  display: -ms-flexbox;
  display: flex;
  height: 31%;
  width: 31%;
  margin-top: 5px;
  margin-bottom: 5px;
  position: relative;
  overflow: hidden;
  -webkit-transition: .6s opacity;
  color: white;
  border: 1px solid gray;
}

.class-button:hover{
  opacity: .64;
  cursor: pointer;
  color: black;
}

.class-button-label{
  height: 24%;
  width: 100%;
  font-size: 130%;
  font-style: bold;
  position: absolute;
  bottom: 0;
  background-color: #DAA520;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.card-container{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 700px;
  height: 520px;
}

.card-item{
  width: 175px;
  height: 250px;
  background-size: cover;
}

.card-item:hover{
  cursor: pointer;
  opacity: .80;
}

.deckbuilder-container{
  width: 1000px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-direction: row;
      flex-direction: row;
}
.gallery-container{
  display:-ms-flexbox;
  display:flex;
}
.page-button{
  width: 50px;
  height: 520px;
  cursor: pointer;
}

.decklist-container{
  height: 558px;
  width: 200px;
}

.decklist-header{
  height: 80px;
  width: auto;
  background-size: 100% 100%;
  font-size: 20px;
  font-style: bold;
  color: white;
  line-height: 90px;
}

.decklist-item{
  display: -ms-flexbox;
  display: flex;
  height: 35px;
  background-position: 18% 24%;
  background-color: black;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
  color: white;
}

.decklist-item:hover{
  cursor: pointer;
  opacity: .86;
}

.quantity{
  height: 100%;
  padding-right: 10px;
  background-color: grey;
}

.back-button{
  font-size: 15px;
  width: 500px;
  height: 50px;
  border-radius: 10px;
  background-color: #87cefa;
  margin-left: 160px;

}

.back-button:hover{
  cursor: pointer;
  color: white;
  opacity: .78;
}




